import { Component } from '@angular/core';
import { name } from './n1';
import { names } from './n2';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'cjhch04';

  navs = name;

  showNavBar = true;

  menuIndex = 0;
  checknav!: names;
  
  toggleNavBar() {
    this.showNavBar = !this.showNavBar;
  }
  public flag = false; 
  changcss(n: names) {
    this.checknav = n;
    if (this.checknav.active) {
      n.active = false;
    } else {
      n.active = true;
    }
}

}
